// Colors
$purple: pink;
$cream: #FFF4D5;
$burnt-orange: #E17055;
$grey-dark: #2C3A47;
$grey: #bbbbbb;


// Typography
$font-family: 'Arial';
$text-color: $grey-dark;

$accent-color: $burnt-orange;

$text-header: 36px $font-family, sans-serif, bold;
$text-body: 16px $font-family, sans-serif, regular;

// Media Query
$max-width-small-screen: 767px;
$max-width-medium-screen: 1024px;
$min-width-large-screen: 1025px;

