@import "../breakpoints.scss";

// General
body {
    margin: 20px 0;
    font: $text-body;
}

//Typography
h1 {
    color: $text-color;
    display: block;
    font: $text-header;
    font-weight: bold;
    margin-bottom: 30px;
    position: relative;
}

// Layout
.container {
    width: 100%;
    padding: 0 10px;
}


// Accessability
.screen-reader-only {
    border: 0;
    clip: rect(0, 0, 0, 0);
    height: 0;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
}

@include tablet {
    .container {
        padding: 0 15px;
    }
}

@include desktop {
    .container {
        margin: 0 auto;
        max-width: 980px;
    }
}