@import "../breakpoints.scss";
@import "../variables.scss";

.grocery-list {
    padding-left: 0;
    list-style: none;

    &--item-wrapper {
        background: $cream;
        border-bottom: 5px solid $accent-color;
        border-right: 5px solid $accent-color;
        display: inline-block;
        line-height: 150%;
        margin-bottom: 20px;
        min-height: 200px;
        padding: 10px;
        position: relative;
        transform: scale(1) translateY(0);
        transition: all ease .3s;
        width: 100%;

        &:hover {
            transform: scale(1.03) translateY(-5px);
        }
    }

    &--item-name-wrapper {
        position: absolute;
        width: 100%;
        top: 50%;
        margin-top: -33px;
        padding-right: 20px;
    }

    h2.grocery-list--item-name {
        color: $text-color;
        display: block;
        font: $text-header;
        font-weight: bold;
        margin-bottom: 2px;
        position: relative;

        &::after {
            background-color: $grey;
            bottom: -2px;
            content: "";
            height: 2px;
            left: 0;
            position: absolute;
            width: 100%;
        }
    }

    &--item-name-details-wrapper {
        display: block;
        line-height: 18px;
        margin-top: 5px;
    }

    &--item-brand,
    &--item-type {
        font-weight: bold;
        font-size: 12px;
        margin-right: 5px;
    }

    &--item-category,
    &--item-qty-wrapper {
        color: $accent-color;
        font-weight: bold;
        position: absolute;
        text-transform: uppercase;
    }

    &--item-category {
        bottom: 5px;
        left: 10px;
    }

    &--item-qty {
        font-weight: bold;
    }

    &--item-qty-wrapper {
        top: 5px;
        right: 10px;
    }
}

@include tablet {
    .grocery-list {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        margin-right: -20px;
    }

    .grocery-list--item-wrapper {
        margin-bottom: 50px;
        margin-right: 20px;
        min-width: 200px;
        width: 200px;
    }
}

@include desktop {
    .grocery-list {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        margin-right: -20px;
    }
    
    .grocery-list--item-wrapper {
        margin-bottom: 50px;
        margin-right: 20px;
        min-width: 200px;
        width: 200px;
    }
}